html {
  scroll-behavior: smooth;
}

body {
  font-family: $default-font;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.83;
  letter-spacing: 0.04em;
  color: $black;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

a {
  text-decoration: none;
}

.container {
  padding-right: 10px;
  padding-left: 10px;
  margin-right: auto;
  margin-left: auto;
  @include for-size(mobile) {
    width: 300px;
  }
  @include for-size(tablet) {
    width: 720px;
  }
  @include for-size(desktop) {
    width: 1080px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.flexbox {
  display: flex;
}

.section__title {
  font-family: $logo-font;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: $accent;
  text-transform: uppercase;
  @include for-size(tablet) {
    font-size: 48px;
  }
  @include for-size(desktop) {
    font-size: 58px;
    letter-spacing: 0.06em;
  }
}
.section__subtitle {
  font-family: $logo-font;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.17;
  letter-spacing: 0em;
  color: $title-color;
  text-transform: uppercase;

  @include for-size(tablet) {
    font-size: 26px;
    line-height: 1.15;
  }
  @include for-size(desktop) {
    font-size: 30px;
    letter-spacing: 0.04em;
  }
}
