.wrapper-fluid {
  width: 100%;
  background-color: $background-color;

  @media screen and (max-width: 767px) {
    background-image: url(../images/mobile/sectionbg1_mob.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left top 765px;
    padding-bottom: 0;
  }
}

.tradition-wrapper {
  @include for-size(mobile) {
    position: relative;
    margin-bottom: 110px;
    margin: 0 auto;
    padding-top: 130px;
  }

  &__button {
    font-weight: 700;
    display: inline-block;
    border: none;
    width: 176px;
    font-size: 14px;
    height: 44px;
    margin-bottom: 39px;

    @media screen and (max-width: 767px) {
      margin-left: 30px;
    }
  }

  &__strelka {
    width: 8px;
    height: 10px;
    margin-left: 13.62px;
  }
  &__main-title {
    line-height: 38px;
    text-transform: uppercase;

    text-align: center;
    font-size: 18px;
    margin-bottom: 15px;
    font-size: 18px;
    margin: 0;

    color: $hero-background;
  }

  &__sub-title {
    font-size: 34px;
    line-height: 1.14;
    text-align: center;
    display: block;
    margin-bottom: 24px;

    color: $accent;
    text-transform: uppercase;
  }

  &__title {
    @media screen and (max-width: 767px) {
      width: 220px;
      margin-left: auto;
      margin-right: auto;
      letter-spacing: 0;
    }

    font-weight: 500;

    font-family: $default-font;
    line-height: 1.62;
    font-size: 12px;
    color: $black;
    margin-bottom: 45px;
  }

  &__text {
    @media screen and (max-width: 767px) {
      width: 220px;
      margin-left: auto;
      margin-right: auto;
      letter-spacing: 0;
    }

    font-family: $default-font;
    line-height: 19px;
    font-size: 13px;
    letter-spacing: 0.13px;
    color: $text-color;
    margin-bottom: 20px;
    font-weight: 400;

    &:nth-last-child(2n) {
      margin-bottom: 17px;
    }

    &__cow-container-picture {
      display: block;
      margin-left: 20px;
      margin-right: 20px;
      margin-bottom: 46px;
    }

    &__cow-container-information {
      margin-bottom: 40px;
    }

    &__cow-picture {
      margin-right: 74px;
    }

    &-icecream {
      padding-left: 34px;
      padding-right: 80px;

      &__type {
        margin-bottom: 40px;

        &:last-child {
          margin-bottom: 0;
        }
      }
      &__icon {
        margin-bottom: 16px;
      }

      &__massa {
        margin: 0px;
        color: $accent;
        font-family: $logo-font;
        font-size: 60px;
        letter-spacing: 2.4px;
        line-height: 52px;
        text-transform: uppercase;
        font-size: 36px;
      }

      &__descripsion {
        font-size: 12px;
        font-family: $default-font;
        font-weight: 700;
        letter-spacing: 0.13px;
        line-height: 20, 4px;
        color: $black;
      }
    }
    &__pictures {
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 0;
      @media screen and (min-width: 320px) {
        margin-top: 97px;
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .tradition-wrapper {
    @include for-size(tablet) {
      margin: 0 auto;
      margin-bottom: 140px;
    }

    &__main-title {
      font-size: 26px;
      margin-bottom: 16px;
    }

    &__sub-title {
      font-size: 51px;
      margin-bottom: 50px;
    }

    &__button {
      margin-bottom: 66px;
      font-size: 16px;
    }

    &__cow-container-information {
      width: 47%;
      padding-left: 0;
      padding-right: 36px;
      margin-bottom: 0;
    }

    &__title {
      font-size: 14px;
      line-height: 25px;
    }

    &__text {
      font-size: 14px;
      line-height: 25px;
      // display: none;

      &:nth-last-child(2n) {
        font-size: 14px;
        margin-bottom: 62px;
      }
    }

    &-icecream {
      display: flex;
      justify-content: space-between;
      padding-left: 30px;
      padding-right: 112px;

      &__type {
        max-width: 153px;
        margin-bottom: 0;
      }
    }

    &__cow-container {
      display: flex;
      justify-content: space-between;
    }

    &__pictures {
      margin-bottom: 0;
    }
  }
}

@media screen and (min-width: 1280px) {
  .tradition-wrapper {
    @include for-size(desktop) {
      width: 100%;
      margin: 0 auto;
      margin-bottom: 106px;
    }

    background-image: url(../images/desktop/sectionbg1__destop.png);
    background-repeat: no-repeat;
    background-size: 100% 400px;
    background-position: left;
    padding-bottom: 0;

    &__pictures {
      margin: 0 auto;
      margin-bottom: 0;
    }
    &__main-title {
      font-size: 30px;
      margin-bottom: 15px;
    }

    &__sub-title {
      font-size: 60px;
      margin-bottom: 55px;
    }

    &__cow-container-information {
      width: 50%;
    }

    &__title {
      font-size: 17px;
      letter-spacing: 0.17px;
      line-height: 28px;
      max-width: 463px;
    }

    &__text {
      font-size: 17px;
      line-height: 28px;
      letter-spacing: 0.17px;
      max-width: 463px;

      &:nth-last-child(2n) {
        font-size: 17px;
      }
    }
    &__button {
      margin-bottom: 146px;
      font-size: 16px;
    }

    &-icecream {
      display: flex;
      padding-left: 115px;
      padding-right: 115px;

      &__type {
        min-width: 268px;

        &__icon {
          margin-bottom: 40px;
        }

        &__massa {
          font-size: 60px;
          margin-bottom: 15px;
        }

        &__descripsion {
          font-size: 17px;
        }
      }
    }

    &__cow-container {
      &-picture {
        margin: 0 auto;

        height: auto;
      }
    }
    &__cow-container {
      display: flex;
      justify-content: space-between;
    }
  }
}
